import { SponsorProps } from "../res/sponsors";
import React from "react";
import * as styles from "./sponsors.module.css";

type TierProps = {
  title: string;
  tierSponsors: SponsorProps[];
};

const Tier: React.FC<TierProps> = ({ title, tierSponsors }) => {
  return (
    <>
      <h2 className={"title"}>{title}</h2>
      <div className={styles.sponsorList}>
        {tierSponsors.map((item, index) => (
          <a
            key={title + index}
            className={styles.sponsor}
            target={"_blank"}
            rel={"noreferrer"}
            href={item.link}
          >
            <img
              src={item.image}
              alt={item.name}
              className={styles.sponsorImage}
            />
          </a>
        ))}
      </div>
    </>
  );
};

export default Tier;
