import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { sponsors } from "../res/sponsors";
import { currentEdition } from "../../../data";
import Tier from "./tier";
import ContactButton from "../../elements/buttons/contactButton";
import * as styles from "./sponsors.module.css";

const Sponsors: React.FC = () => {
  const { t } = useTranslation();

  const currentSponsors = sponsors.filter((sponsor) =>
    sponsor.edition.includes(currentEdition),
  );
  const pastSponsors = sponsors.filter(
    (sponsor) =>
      sponsor.pledge > 0 &&
      sponsor.edition.filter((ed) => ed != currentEdition).length > 0,
  );
  const partners = sponsors.filter((sponsor) => sponsor.pledge === 0);

  return (
    <div className={`page ${styles.sponsors}`} id={"sponsors"}>
      <Tier title={t("sponsors")} tierSponsors={currentSponsors} />
      <Tier title={t("pastSponsors")} tierSponsors={pastSponsors} />
      <Tier title={t("partners")} tierSponsors={partners} />

      <p style={{ textAlign: "center" }}>{t("contactDetails")}</p>
      <ContactButton />
    </div>
  );
};

export default Sponsors;
