import React from "react";
import { TypeAnimation } from "react-type-animation";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { getAnnouncement } from "../../../hooks/useEditionData";
import { currentEdition } from "../../../data";
import { Announcement } from "../types";

const Announcements: React.FC = () => {
  const { t } = useTranslation();
  const messages = t("announcements", {
    returnObjects: true,
  }) as Record<Announcement, String>;
  return (
    <div className={"text-muted"}>
      [{" "}
      <TypeAnimation
        sequence={[
          messages["return"] as string,
          3000,
          messages[getAnnouncement(currentEdition)] as string,
          3000,
        ]}
        speed={50}
        repeat={Infinity}
      />
      ]
    </div>
  );
};

export default Announcements;
